.accordion {
  display: grid;
  justify-content: center;
  grid-template-columns: 1fr;
  gap: var(--ultrasmall-offset) 0;

  &__item {
    border-radius: 4px;
    padding: var(--second-offset);
    box-shadow: 10px 8px 39px rgba(192, 192, 192, 0.25);
    background-color: var(--color-white);
    cursor: pointer;

    &--opened {
      .accordion__content {
        margin-top: var(--presecond-offset);
      }

      .accordion__text {
        opacity: 1;
        visibility: visible;
      }

      .accordion__line:nth-child(2) {
        transform: translate(-50%, -50%) rotate(0deg);
      }
    }

    @include mobile {
      padding: var(--second-offset) 10px;
    }
  }

  &__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;

    &.focus-visible {
      outline: 1px solid var(--color-black);
    }
  }

  &__title {
    font: var(--bold-font);
    font-family: "Proxima Nova", sans-serif;

    @include small-tablet {
      font-size: 20px;
    }

    @include mobile {
      margin: 0 auto;
      max-width: 227px;
      font-size: 18px;
    }
  }

  &__icon {
    position: relative;
    width: 32px;
    height: 32px;
  }

  &__line {
    position: absolute;
    left: 50%;
    top: 50%;
    width: 32px;
    height: 3px;
    background-color: var(--color-black);
    transform: translate(-50%, -50%);
    transition: transform 0.3s ease-in-out;

    &:nth-child(2) {
      transform: translate(-50%, -50%) rotate(90deg);
    }

    @include small-tablet {
      width: 24px;
    }
  }

  &__content {
    height: 0;
    transition:
      height 0.3s ease-in-out,
      margin-top 0.3s ease-in-out;
  }

  &__text {
    max-width: 800px;
    font: var(--semi-font);
    color: var(--color-port-gore);
    opacity: 0;
    visibility: hidden;
    transition:
      opacity 0.3s ease-in-out,
      visibility 0.3s ease-in-out;
  }
}