.faq {
  padding-top: var(--supbig-offset);
  background-color: var(--color-bg);

  &__container {
    position: relative;
  }

  &__title {
    margin: 0 auto;
    margin-bottom: var(--supbig-offset);
    max-width: 300px;
    text-align: center;
  }

  &__form {
    width: calc(100% + 160px);
    transform: translate(-80px, 35%);
  
    @media (max-width: 1778px) {
      width: 100%;
      transform: translate(0, 40%);
    }

    @include tablet {
      width: 100%;
      transform: translate(0, 15%);
    }
  }

  &__img {
    position: absolute;
    right: -50px;
    top: -250px;
    z-index: var(--z-faq-illustration);
    width: 456px;
    height: 472px;

    svg {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    @media (max-width: 1024px) {
      right: -240px;
    }

    @media (max-width: 654px) {
      top: -100px;
    }
  }

  .container-2 {
    padding: 0;
    max-width: var(--design-mw-2);
  }
}