.location {
  padding: 250px 0 var(--big-offset);

  &__wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 150px;

    @include small-tablet {
      display: block;
      margin-bottom: 80px;
    }
  }

  address {
    margin-right: var(--big-offset);

    @include small-tablet {
      margin-bottom: 40px;
    }
  }

  &__address-title {
    margin-bottom: var(--supsecond-offset);

    @include small-tablet {
      margin-bottom: 35px;
    }
  }

  &__subtitle {
    margin-bottom: var(--small-offset);
    font-family: "Roboto", sans-serif;
    font-weight: 700;
    font-style: normal;
    font-size: 30px;
    color: var(--color-port-gore);
  }

  &__address {
    margin-bottom: var(--second-offset);
    max-width: 210px;
    font-family: "Roboto", sans-serif;
    font-weight: 400;
    font-style: normal;
    font-size: 16px;
    line-height: 1.5;
    color: var(--color-mine-shaft);
  }

  &__btn {
    padding: var(--presecond-offset) var(--second-offset);
    font-weight: 600;

    @include mobile {
      padding: var(--presecond-offset) 0;
      width: 100%;
    }
  }

  &__map {
    width: 750px;
    height: 450px;

    @include small-tablet {
      width: 100%;
    }
  }

  &__title {
    margin-bottom: var(--supsecond-offset);
    text-align: center;
  }

  &__mini-maps {
    display: grid;
    justify-content: center;
    grid-template-columns: repeat(3, 1fr);
    gap: var(--second-offset);

    @include small-tablet {
      grid-template-columns: minmax(200px, 400px);
    }
  }

  &__mini-map {
    overflow: hidden;

    img {
      width: 100%;
      height: 256px;
      box-shadow: 10px 8px 39px rgba(136, 131, 131, 0.25);
      object-fit: cover;
    }
  }

  &__desc {
    margin-top: var(--second-offset);
    font-family: "Roboto", sans-serif;
    font-weight: 400;
    font-size: 16px;
    line-height: 1.5;
    text-align: center;
    color: var(--color-mine-shaft);
  }

  @include small-tablet {
    padding: 140px 0 80px;
  }
}