.features {
  margin-bottom: 200px;
  padding: var(--supbig-offset) 0;
  background-color: var(--color-bg);

  &__container {
    position: relative;
  }

  &__title {
    margin: 0 auto;
    margin-bottom: var(--supsecond-offset);
    max-width: 700px;
    text-align: center;
  }

  &__cards {
    display: grid;
    justify-content: center;
    grid-template-columns: repeat(2, minmax(300px, 614px));
    gap: 8px;

    @include tablet {
      grid-template-columns: minmax(300px, 614px);
    }
  }

  &__img {
    position: absolute;
    left: 30px;
    bottom: -290px;
    z-index: var(--z-features-illustration);
    width: 252px;
    height: 252px;

    svg {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    @media (max-width: 1024px) {
      display: none;
    }
  }

  @include tablet {
    margin-bottom: 80px;
  }
}