.form {
  padding: var(--prebig-offset);
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  background-color: var(--color-white);

  &__desc {
    margin-bottom: var(--second-offset);
    font: var(--bold-font);
    color: var(--color-mine-shaft);
  }

  &__wrapper {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    margin-bottom: var(--presecond-offset);
    border: none;
    padding: 0;
    gap: var(--presecond-offset);

    @include tablet {
      grid-template-columns: repeat(2, 1fr);
    }

    @include small-tablet {
      grid-template-columns: 1fr;
    }
  }

  &__input {
    outline: none;
    border: 1px solid var(--color-mine-shaft);
    padding: var(--presecond-offset);
    width: 100%;
    font: var(--regular-font);
    font-size: 16px;
    
    &::placeholder {
      color: var(--color-mine-shaft);
    }

    @include mobile {
      padding: 18px;
    }
  }

  &__submit {
    font-weight: 600;
    text-transform: uppercase;

    @include small-tablet {
      padding: 22px 0;
    }

    @include mobile {
      padding: 18px 0;
    }
  }

  @include mobile {
    padding: var(--prebig-offset) 15px;
  }
}