.gallery {
  padding: var(--supbig-offset) 0;
  background-color: var(--color-white);

  &__container {
    display: grid;
    justify-content: center;
    grid-auto-rows: 196px;
    grid-template-columns: repeat(4, 1fr);
    gap: var(--presecond-offset);

    @include tablet {
      grid-template-columns: repeat(2, 1fr);
    }

    @include mobile {
      grid-template-columns: 1fr;
    }
  }

  &__item {
    position: relative;
    overflow: hidden;
    border-radius: 8px;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    &--big {
      grid-column: span 2;
      grid-row: span 2;

      @include mobile {
        grid-column: span 1;
      }
    }
  }

  &__shape {
    position: absolute;
    left: -10%;
    top: -68%;
    border-radius: 100%;
    width: 500px;
    height: 500px;
    background-color: rgba(24, 25, 64, 0.7);
    backdrop-filter: blur(4px);

    &--orange {
      left: -18%;
      top: -62%;
      background-color: rgba(113, 74, 37, 0.5);
    }
  }

  &__text {
    position: absolute;
    left: 7%;
    top: 10%;
    z-index: var(--z-shape-text);
    max-width: 320px;
    font: var(--prebig-font);
    text-transform: uppercase;
    color: var(--color-white);

    span {
      -webkit-text-stroke: 1px var(--color-white);
      color: transparent;
    }
  }
}