.faculty {
  padding-bottom: 180px;

  &__title {
    margin: 0 auto;
    margin-bottom: var(--middle-offset);
    max-width: 500px;
    text-align: center;
  }

  &__list {
    display: grid;
    align-items: flex-start;
    grid-template-columns: repeat(4, 1fr);
    gap: var(--second-offset);

    @media (max-width: 1440px) {
      grid-template-columns: repeat(2, 1fr);
    }

    @include small-tablet {
      grid-template-columns: 1fr;
    }
  }

  // Item

  .item {
    background-color: var(--color-white);

    &__name {
      margin-bottom: var(--second-offset);
      padding: var(--second-offset) 0;
      font: var(--heavy-font);
      font-size: 20px;
      letter-spacing: 3px;
      text-align: center;
      text-transform: uppercase;
      color: var(--color-white);
      background-color: var(--color-port-gore);
      transition: background-color 0.3s ease-in-out;

      @include small-tablet {
        padding: 25px 0;
        font-size: 16px;
      }
    }

    &__group:not(:last-child) {
      margin-bottom: var(--second-offset);
    }

    &__title {
      margin-bottom: var(--presecond-offset);
      font: var(--bold-font);
      font-weight: 800;
      color: var(--color-port-gore);
    }

    &__salary {
      font: var(--heavy-font);
      color: var(--color-dallas);
    }

    @include desktop {
      &:hover .item__name {
        background-color: var(--color-dallas);
      }
    }
  }

  @include tablet {
    padding-bottom: 80px;
  }
}