.education {
  padding-bottom: 160px;

  &__container {
    position: relative;
  }

  &__title {
    margin-bottom: var(--supsecond-offset);
    max-width: 300px;
  }

  &__cards {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    margin-bottom: var(--prebig-offset);
    gap: var(--supsecond-offset) 180px;

    @include tablet {
      gap: var(--supsecond-offset);
    }

    @include small-tablet {
      grid-template-columns: 1fr;
    }
  }

  &__wrapper {
    text-align: center;
  }

  &__btn {
    padding: var(--presecond-offset) var(--second-offset);
    font-weight: 600;
  }

  &__img {
    position: absolute;
    right: -135px;
    bottom: -350px;
    z-index: var(--z-education-illustration);
    width: 628px;
    height: 539px;

    svg {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    @media (max-width: 1400px) {
      right: -150px;
    }

    @media (max-width: 1024px) {
      right: -400px;
      bottom: -310px;
    }

    @media (max-width: 576px) {
      display: none;
    }
  }

  // Card

  .card {
    position: relative;

    &__option {
      font: var(--bold-font);
      line-height: 1;
      text-transform: uppercase;
      color: var(--color-science-blue);

      @include tablet {
        font-size: 24px;
      }

      @include small-tablet {
        font-size: 22px;
      }
    }

    &__education-type {
      margin-bottom: var(--ultrasmall-offset);
      font: 900 50px / 1.625 var(--font-family);
      color: var(--color-port-gore);

      @include tablet {
        font-size: 40px;
      }

      @include small-tablet {
        font-size: 30px;
      }
    }

    &__text {
      max-width: 650px;
      font: var(--supmedium-font);
    }

    &__img {
      position: absolute;
      right: 20px;
      top: 70%;
      z-index: -1;
      width: 105px;
      height: 159px;
      transform: translateY(-70%);

      svg {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }

      @include small-tablet {
        display: none;
      }
    }
  }

  @include tablet {
    padding-bottom: 80px;
  }

  @include mobile {
    padding-bottom: 50px;
  }
}