:root {
  // Colors
  --color-black: #000;
  --color-white: #fff;
  --color-bg: #eff2f9;
  --color-science-blue: #003adc;
  --color-science-blue--hover: #3c6ffd;
  --color-science-blue--active: #0042f8;
  --color-klein-blue: #082caf;
  --color-mine-shaft: #333;
  --color-port-gore: #181940;
  --color-dallas: #714a25;
  --color-radical-red: #ff3a4f;

  // Offsets
  --container-horizontal-offset: 8px;
  --design-mw: 1600px;
  --design-mw-2: 1366px;
  --mw: calc(var(--design-mw) + var(--container-horizontal-offset) * 2);
  --mw-2: calc(var(--design-mw-2) + var(--container-horizontal-offset) * 2);
  --supbig-offset: 130px;
  --big-offset: 100px;
  --prebig-offset: 80px;
  --middle-offset: 70px;
  --supsecond-offset: 50px;
  --second-offset: 30px;
  --presecond-offset: 20px;
  --small-offset: 14px;
  --ultrasmall-offset: 12px;

  // Fonts
  --font-family: "Gilroy", sans-serif;
  --big-font: 900 115px / 0.95 var(--font-family);
  --prebig-font: 900 36px / 1.16 var(--font-family);
  --high-font: 800 60px / 1.13 var(--font-family);
  --heavy-font: 800 24px / 1.2 var(--font-family);
  --bold-font: 700 32px / 1.25 var(--font-family);
  --semi-font: 600 20px / 1.6 var(--font-family);
  --supmedium-font: 400 24px / 1.4 var(--font-family);
  --medium-font: 400 20px / 1 var(--font-family);
  --regular-font: 400 27px / 1.2 var(--font-family);

  // Z-indexes
  --z-section: 10;
  --z-hero-illustration: 100;
  --z-education-illustration: 0;
  --z-features-illustration: 100;
  --z-diploma-illustration: 100;
  --z-faq-illustration: 100;
  --z-modal: 1000;
  --z-shape-text: 10;
  --z-magnifier-icon: 10;
}

@media (max-width: 1280px) {
  :root {
    --prebig-offset: 50px;
  }
}

@include tablet {
  :root {
    // Offsets
    --supbig-offset: 60px;
    --prebig-offset: 45px;
    --middle-offset: 40px;

    // Fonts
    --big-font: 900 96px / 0.95 var(--font-family);
    --high-font: 800 50px / 1.13 var(--font-family);
    --heavy-font: 800 20px / 1.2 var(--font-family);
    --bold-font: 700 28px / 1.25 var(--font-family);
    --semi-font: 600 18px/1.6 var(--font-family);
    --medium-font: 400 16px / 1 var(--font-family);
    --supmedium-font: 400 18px / 1.4 var(--font-family);
    --regular-font: 400 24px / 1.2 var(--font-family);
  }
}

@include small-tablet {
  :root {
    // Offsets
    --prebig-offset: 30px;

    // Fonts
    --big-font: 900 70px / 0.95 var(--font-family);
    --prebig-font: 900 30px / 1.16 var(--font-family);
    --high-font: 800 40px / 1.13 var(--font-family);
    --heavy-font: 800 18px / 1.3 var(--font-family);
    --bold-font: 700 25px / 1.25 var(--font-family);
    --semi-font: 600 16px / 1.6 var(--font-family);
    --supmedium-font: 400 16px / 1.4 var(--font-family);
    --regular-font: 400 20px / 1.2 var(--font-family);
  }
}

@include mobile {
  :root {
    // Offsets
    --supbig-offset: 50px;
    --prebig-offset: 20px;

    // Fonts
    --big-font: 900 56px / 0.9 var(--font-family);
    --high-font: 800 35px / 1.2 var(--font-family);
    --bold-font: 700 22px / 1.4 var(--font-family);
    --semi-font: 600 14px / 1.4 var(--font-family);
    --medium-font: 400 14px / 1 var(--font-family);
  }
}

// Hero Illustration

@media (max-width: 1450px) {
  :root {
    --z-hero-illustration: 0;
  }
}

// Features Illustration 

@media (max-width: 1024px) {
  :root {
    --z-features-illustration: 0;
  }
}

// Features Illustration

@media (max-width: 1600px) {
  :root {
    --z-diploma-illustration: 0;
  }
}

// Faq Illustration

@media (max-width: 1280px) {
  :root {
    --z-faq-illustration: 0;
  }
}