.diploma {
  padding: var(--supbig-offset) 0;
  background-color: var(--color-bg);

  &__container {
    position: relative;
    display: flex;
    align-items: center;

    @include tablet {
      display: block;
    }
  }

  &__group {
    margin-right: var(--supsecond-offset);

    @include tablet {
      margin-bottom: 40px;
    }
  }

  &__title {
    margin-bottom: var(--supsecond-offset);
    max-width: 420px;

    @include small-tablet {
      margin-bottom: 25px;
    }
  }

  &__info {
    margin-bottom: var(--second-offset);
    font: var(--bold-font);
    color: var(--color-port-gore);
  }

  &__text {
    max-width: 684px;
    font: var(--heavy-font);
    font-weight: 500;
    color: var(--color-port-gore);

    @include small-tablet {
      font-size: 16px;
    }
  }

  &__list {
    display: grid;
    flex-basis: 500px;
    grid-template-columns: repeat(3, 1fr);
    gap: var(--supsecond-offset) var(--small-offset);

    @include tablet {
      gap: var(--small-offset);
    }

    @include mobile {
      grid-template-columns: repeat(2, 1fr);
    }
  }

  &__item {
    position: relative;
    padding: var(--small-offset) 0;
    box-shadow: 6px 6px 17px rgba(181, 181, 181, 0.25);
    text-align: center;
    background-color: var(--color-white);
    cursor: pointer;

    svg {
      position: absolute;
      left: 50%;
      top: 50%;
      z-index: var(--z-magnifier-icon);
      width: 30px;
      height: 30px;
      object-fit: cover;
      opacity: 0;
      transform: translate(-50%, -50%);
      transition:
        opacity 0.3s ease-in-out,
        transform 0.3s ease-in-out;
    }

    img {
      width: 120px;
      height: 176px;
      object-fit: cover;
      transition: transform 0.3s ease-in-out;
    }

    &.focus-visible {
      img {
        transform: translateY(-15px);
      }

      svg {
        opacity: 1;
        transform: translate(-50%, calc(-50% - 15px));
      }
    }

    @include desktop {
      &:hover {
        img {
          transform: translateY(-15px);
        }
  
        svg {
          opacity: 1;
          transform: translate(-50%, calc(-50% - 15px));
        }
      }
    }
  }

  &__img {
    position: absolute;
    right: -45px;
    top: -280px;
    z-index: var(--z-diploma-illustration);
    width: 316px;
    height: 465px;

    svg {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    @media (max-width: 1024px) {
      right: -140px;
      top: 0;
    }
  }
}