.representatives {
  padding: var(--supbig-offset) 0;
  background-color: var(--color-bg);

  &__title {
    margin: 0 auto;
    margin-bottom: var(--supsecond-offset);
    max-width: 850px;
    word-wrap: break-word;
    text-align: center;
  }

  &__list {
    display: grid;
    justify-content: center;
    grid-template-columns: repeat(3, 1fr);
    gap: var(--second-offset);

    @include tablet {
      grid-template-columns: minmax(300px, 480px);
    }
  }

  // People

  .people {
    &__avatar {
      display: inline-block;
      margin-bottom: var(--supsecond-offset);
      width: 100%;
      height: 410px;
      object-fit: cover;

      @include tablet {
        margin-bottom: 15px;
      }

      @include mobile {
        height: 280px;
      }
    }

    &__fullname {
      margin-bottom: var(--presecond-offset);
      font: var(--heavy-font);
      color: var(--color-black);
    }

    &__info {
      max-width: 465px;
      font: var(--semi-font);
      color: var(--color-port-gore);
    }
  }
}