.hero {
  margin-bottom: 270px;
  padding-top: var(--big-offset);
  background-color: var(--color-bg);

  &__qualification {
    margin-bottom: var(--small-offset);

    @include mobile {
      margin-bottom: 20px;
    }
  }

  &__headline {
    position: relative;
    z-index: var(--z-section);
    margin: 0;
    margin-bottom: var(--middle-offset);
    font: var(--big-font);
    color: var(--color-science-blue);

    span {
      color: var(--color-dallas);
    }

    @include small-tablet {
      margin-bottom: 20px;
    }

    @include mobile {
      margin-bottom: 40px;
    }
  }

  &__form {
    width: calc(100% + 160px);
    transform: translate(-80px, 40%);

    @media (max-width: 1778px) {
      width: 100%;
      transform: translate(0, 40%);
    }

    @include tablet {
      width: 100%;
      transform: translate(0, 15%);
    }

    @include mobile {
      transform: translate(0, 5%);
    }
  }

  @include tablet {
    margin-bottom: 120px;
  }

  @include small-tablet {
    padding-top: 60px;
  }

  @include mobile {
    margin-bottom: 80px;
  }
}

// Qualification

.qualification {
  display: flex;
  flex-wrap: wrap;
  align-items: center;

  &__item {
    position: relative;
    font: var(--heavy-font);
    color: var(--color-port-gore);

    &:not(:last-child) {
      margin-right: var(--presecond-offset);

      &::after {
        content: "";
        position: absolute;
        right: -10px;
        top: 50%;
        display: block;
        width: 1px;
        height: 100%;
        background-color: var(--color-port-gore);
        transform: translateY(-50%);
      }
    }

    @include mobile {
      font-size: 16px;
      line-height: 1.8;
    }
  }
}

// Profession

.profession {
  position: relative;

  &__wrapper {
    position: relative;
    z-index: var(--z-section);
  }

  &__desc {
    margin-bottom: var(--second-offset);
    font: var(--heavy-font);
    font-size: 27px;
    color: var(--color-port-gore);

    @include small-tablet {
      font-size: 20px;
    }

    @include mobile {
      font-size: 18px;
      line-height: 1.6;
    }
  }

  &__list {
    max-width: 884px;

    @include mobile {
      display: none;
    }
  }

  &__item {
    display: inline-block;
    margin: 0 var(--ultrasmall-offset) var(--ultrasmall-offset) 0;
    border-radius: 10px;
    padding: 5px 14px;
    font: var(--regular-font);
    color: var(--color-black);
    background-color: #d7dff6;

    @include small-tablet {
      margin: 0 8px 8px 0;
      padding: 8px 14px;
    }
  }

  &__img {
    position: absolute;
    right: -60px;
    top: 30%;
    z-index: var(--z-hero-illustration);
    width: 649px;
    height: 500px;
    transform: translateY(-30%);

    svg {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    @media (max-width: 1224px) {
      right: -300px;
      top: -350px;
      transform: none;
    }

    @media (max-width: 1024px) {
      top: -280px;
    }

    @media (max-width: 768px) {
      top: -50px;
    }
  }
}