html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

body {
  font-family: var(--font-family);
  background-color: var(--color-white);

  &.overflowed {
    overflow: hidden;
  }
}

.site-container {
  overflow: hidden;
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-horizontal-offset);
  max-width: var(--mw);

  // .profession__img,
  // .education__img,
  // .features__img,
  // .diploma__img,
  // .faq__img {
  //   display: none;
  // }

  &-2 {
    margin: 0 auto;
    padding: 0 var(--container-horizontal-offset);
    max-width: var(--mw-2);
  }
}

.btn-reset {
  outline: none;
  border: none;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
}

.visually-hidden {
  position: absolute;
  clip-path: inset(50%);
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip: rect(0 0 0 0);
}

.main-screen {
  position: relative;
  z-index: var(--z-section);
}