.scope {
  padding: var(--supbig-offset) 0;
  background-color: var(--color-white);

  &__title {
    margin: 0 auto;
    margin-bottom: var(--supsecond-offset);
    max-width: 850px;
    text-align: center;
  }

  &__list {
    display: grid;
    justify-content: center;
    grid-template-columns: repeat(2, minmax(300px, 614px));
    margin-bottom: var(--middle-offset);
    gap: 8px;

    @include tablet {
      grid-template-columns: minmax(300px, 614px);
    }
  }

  &__wrapper {
    text-align: center;
  }

  &__btn {
    padding: var(--presecond-offset) var(--second-offset);
    font-weight: 600;
  }
}