.partners {
  padding-top: var(--supbig-offset);
  background-color: var(--color-white);

  &__title {
    margin-bottom: var(--big-offset);
    text-align: center;

    @include mobile {
      margin-bottom: 60px;
    }
  }

  &__list {
    display: grid;
    justify-content: center;
    grid-template-columns: repeat(4, minmax(100px, 263px));
    text-align: center;
    gap: var(--supsecond-offset);

    @include tablet {
      grid-template-columns: repeat(3, minmax(80px, 263px));
    }

    @include small-tablet {
      grid-template-columns: repeat(2, minmax(80px, 263px));
    }

    @include mobile {
      grid-template-columns: 263px;
    }
  }

  // Partner

  .partner {
    &:nth-child(9) {
      grid-column: 2 / 3;

      @include tablet {
        grid-column: initial;
      }
    }

    &:nth-child(10) {
      grid-column: 3 / 4;

      @include tablet {
        grid-column: 2 / 3;
      }

      @include mobile {
        grid-column: initial;
      }
    }
  }
}