html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

body {
  font-family: var(--font-family);
  background-color: var(--color-white);
}
body.overflowed {
  overflow: hidden;
}

.site-container {
  overflow: hidden;
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-horizontal-offset);
  max-width: var(--mw);
}
.container-2 {
  margin: 0 auto;
  padding: 0 var(--container-horizontal-offset);
  max-width: var(--mw-2);
}

.btn-reset {
  outline: none;
  border: none;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
}

.visually-hidden {
  position: absolute;
  clip-path: inset(50%);
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip: rect(0 0 0 0);
}

.main-screen {
  position: relative;
  z-index: var(--z-section);
}

.logo {
  display: flex;
  align-items: center;
}
.logo svg {
  width: 30px;
  height: 30px;
  object-fit: cover;
}
.logo__title {
  margin-left: var(--small-offset);
  font-weight: 800;
  font-size: 14px;
  line-height: 0.9;
  letter-spacing: 4px;
}
.logo__title span {
  font-size: 23px;
  letter-spacing: 3px;
}
@media (max-width: 1024px) {
  .logo__title span {
    font-size: 20px;
  }
}
@media (max-width: 576px) {
  .logo__title span {
    font-size: 18px;
  }
}
@media (max-width: 576px) {
  .logo__title {
    margin-left: 10px;
    font-size: 12px;
  }
}
.logo__slogan {
  position: relative;
  margin-left: var(--presecond-offset);
  font-weight: 600;
  font-size: 18px;
  line-height: 1;
  letter-spacing: 1px;
}
.logo__slogan::before {
  content: "";
  position: absolute;
  left: -15px;
  top: 50%;
  display: block;
  width: 1px;
  height: 80%;
  background-color: var(--color-radical-red);
  transform: translateY(-50%);
}
@media (max-width: 1024px) {
  .logo__slogan::before {
    left: -12px;
  }
}
@media (max-width: 1024px) {
  .logo__slogan {
    font-size: 16px;
  }
}
@media (max-width: 768px) {
  .logo__slogan {
    display: none;
  }
}
.logo__title, .logo__slogan {
  max-width: 150px;
  font-family: var(--font-family);
  text-transform: uppercase;
  color: var(--color-klein-blue);
}
@media (max-width: 1024px) {
  .logo__title, .logo__slogan {
    max-width: 135px;
  }
}

.btn {
  font: var(--medium-font);
  color: var(--color-white);
  transition: background-color 0.3s ease-in-out;
}
.btn--primary {
  background-color: var(--color-science-blue);
}
.btn--primary.focus-visible {
  background-color: var(--color-science-blue--hover);
}
@media (min-width: 1025px) {
  .btn--primary:hover {
    background-color: var(--color-science-blue--hover);
  }
  .btn--primary:active {
    background-color: var(--color-science-blue--active);
  }
}

.phone {
  font: var(--medium-font);
  font-weight: 600;
  text-decoration: none;
  color: var(--color-mine-shaft);
}

.form {
  padding: var(--prebig-offset);
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  background-color: var(--color-white);
}
.form__desc {
  margin-bottom: var(--second-offset);
  font: var(--bold-font);
  color: var(--color-mine-shaft);
}
.form__wrapper {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  margin-bottom: var(--presecond-offset);
  border: none;
  padding: 0;
  gap: var(--presecond-offset);
}
@media (max-width: 1024px) {
  .form__wrapper {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 768px) {
  .form__wrapper {
    grid-template-columns: 1fr;
  }
}
.form__input {
  outline: none;
  border: 1px solid var(--color-mine-shaft);
  padding: var(--presecond-offset);
  width: 100%;
  font: var(--regular-font);
  font-size: 16px;
}
.form__input::placeholder {
  color: var(--color-mine-shaft);
}
@media (max-width: 576px) {
  .form__input {
    padding: 18px;
  }
}
.form__submit {
  font-weight: 600;
  text-transform: uppercase;
}
@media (max-width: 768px) {
  .form__submit {
    padding: 22px 0;
  }
}
@media (max-width: 576px) {
  .form__submit {
    padding: 18px 0;
  }
}
@media (max-width: 576px) {
  .form {
    padding: var(--prebig-offset) 15px;
  }
}

.check {
  padding: 1.2em;
}
.check__input:checked + .check__box {
  background-image: url("data:image/svg+xml,%3Csvg width='18' height='18' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Crect x='.5' y='.5' width='17' height='17' rx='4.5' stroke='%23BDC1C9'/%3E%3Cpath d='M14.14 6.104l-.935-.915a.646.646 0 00-.467-.19.646.646 0 00-.468.19L7.755 9.614 5.732 7.628a.646.646 0 00-.467-.189.646.646 0 00-.468.189l-.938.915a.618.618 0 00-.192.458c0 .18.065.333.192.458l2.493 2.437.935.915a.646.646 0 00.468.189c.183 0 .34-.064.468-.189l.935-.915 4.982-4.874a.618.618 0 00.193-.458.615.615 0 00-.193-.46z' fill='%23181940'/%3E%3C/svg%3E");
}
.check__input.focus-visible + .check__box {
  outline: 1px solid var(--color-science-blue);
}
.check__box {
  position: absolute;
  margin-left: -1.2em;
  padding: 9px;
  width: 1em;
  height: 1em;
  background-image: url("data:image/svg+xml,%3Csvg width='18' height='18' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Crect x='.5' y='.5' width='17' height='17' rx='4.5' stroke='%23BDC1C9'/%3E%3C/svg%3E");
  cursor: pointer;
}
.check__text {
  margin-left: var(--ultrasmall-offset);
  font: var(--regular-font);
  font-size: 12px;
  color: var(--color-mine-shaft);
}
.check__text a {
  position: relative;
  display: inline-block;
  text-decoration: none;
  color: var(--color-mine-shaft);
}
.check__text a::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: -1px;
  display: block;
  width: 100%;
  height: 1px;
  background-color: var(--color-mine-shaft);
}
@media (max-width: 1024px) {
  .check__text {
    margin-top: 6px;
    margin-left: 6px;
  }
}

.title {
  font: var(--high-font);
  color: var(--color-port-gore);
}
.title span {
  color: var(--color-dallas);
}

.mini-card {
  display: flex;
  align-items: center;
  padding: var(--presecond-offset) var(--second-offset);
  box-shadow: 10px 8px 39px rgba(192, 192, 192, 0.25);
  background-color: var(--color-white);
}
.mini-card__img {
  margin-right: var(--second-offset);
}
.mini-card__img svg {
  width: 76px;
  height: 76px;
  object-fit: cover;
}
.mini-card__desc {
  font: var(--semi-font);
  color: var(--color-port-gore);
}
@media (max-width: 576px) {
  .mini-card {
    padding: 15px 10px;
  }
}

.accordion {
  display: grid;
  justify-content: center;
  grid-template-columns: 1fr;
  gap: var(--ultrasmall-offset) 0;
}
.accordion__item {
  border-radius: 4px;
  padding: var(--second-offset);
  box-shadow: 10px 8px 39px rgba(192, 192, 192, 0.25);
  background-color: var(--color-white);
  cursor: pointer;
}
.accordion__item--opened .accordion__content {
  margin-top: var(--presecond-offset);
}
.accordion__item--opened .accordion__text {
  opacity: 1;
  visibility: visible;
}
.accordion__item--opened .accordion__line:nth-child(2) {
  transform: translate(-50%, -50%) rotate(0deg);
}
@media (max-width: 576px) {
  .accordion__item {
    padding: var(--second-offset) 10px;
  }
}
.accordion__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}
.accordion__header.focus-visible {
  outline: 1px solid var(--color-black);
}
.accordion__title {
  font: var(--bold-font);
  font-family: "Proxima Nova", sans-serif;
}
@media (max-width: 768px) {
  .accordion__title {
    font-size: 20px;
  }
}
@media (max-width: 576px) {
  .accordion__title {
    margin: 0 auto;
    max-width: 227px;
    font-size: 18px;
  }
}
.accordion__icon {
  position: relative;
  width: 32px;
  height: 32px;
}
.accordion__line {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 32px;
  height: 3px;
  background-color: var(--color-black);
  transform: translate(-50%, -50%);
  transition: transform 0.3s ease-in-out;
}
.accordion__line:nth-child(2) {
  transform: translate(-50%, -50%) rotate(90deg);
}
@media (max-width: 768px) {
  .accordion__line {
    width: 24px;
  }
}
.accordion__content {
  height: 0;
  transition: height 0.3s ease-in-out, margin-top 0.3s ease-in-out;
}
.accordion__text {
  max-width: 800px;
  font: var(--semi-font);
  color: var(--color-port-gore);
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
}

.modal {
  position: fixed;
  left: 0;
  top: 0;
  z-index: var(--z-modal);
  overflow: auto;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
  cursor: pointer;
}
.modal__container {
  position: relative;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  width: 100%;
  height: 100%;
}
.modal__img {
  margin: auto 0;
  width: 350px;
  height: 500px;
  cursor: default;
}
.modal__img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
@media (max-width: 576px) {
  .modal__img {
    margin-right: 0;
  }
}
.modal__close {
  position: absolute;
  right: 0;
  top: 20px;
  width: 30px;
  height: 30px;
}
.modal__close.focus-visible {
  outline: 1px solid var(--color-white);
}
.modal__line {
  position: absolute;
  display: block;
  width: 30px;
  height: 2px;
  background-color: var(--color-white);
}
.modal__line:nth-child(1) {
  transform: rotate(45deg);
}
.modal__line:nth-child(2) {
  transform: rotate(-45deg);
}
.modal--visible {
  opacity: 1;
  visibility: visible;
}

.header {
  padding-top: var(--second-offset);
  background-color: var(--color-bg);
}
.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__btn {
  display: flex;
  align-items: center;
  margin-right: var(--presecond-offset);
  padding: 14px 23px;
}
.header__btn svg {
  margin-right: var(--presecond-offset);
  width: 26px;
  height: 26px;
  object-fit: cover;
}
@media (max-width: 1024px) {
  .header__btn {
    padding: 10px 15px;
  }
}
.header__left, .header__right {
  display: flex;
  align-items: center;
}
.header__logo, .header__btn {
  text-decoration: none;
}
@media (max-width: 768px) {
  .header__right {
    display: none;
  }
}

.hero {
  margin-bottom: 270px;
  padding-top: var(--big-offset);
  background-color: var(--color-bg);
}
.hero__qualification {
  margin-bottom: var(--small-offset);
}
@media (max-width: 576px) {
  .hero__qualification {
    margin-bottom: 20px;
  }
}
.hero__headline {
  position: relative;
  z-index: var(--z-section);
  margin: 0;
  margin-bottom: var(--middle-offset);
  font: var(--big-font);
  color: var(--color-science-blue);
}
.hero__headline span {
  color: var(--color-dallas);
}
@media (max-width: 768px) {
  .hero__headline {
    margin-bottom: 20px;
  }
}
@media (max-width: 576px) {
  .hero__headline {
    margin-bottom: 40px;
  }
}
.hero__form {
  width: calc(100% + 160px);
  transform: translate(-80px, 40%);
}
@media (max-width: 1778px) {
  .hero__form {
    width: 100%;
    transform: translate(0, 40%);
  }
}
@media (max-width: 1024px) {
  .hero__form {
    width: 100%;
    transform: translate(0, 15%);
  }
}
@media (max-width: 576px) {
  .hero__form {
    transform: translate(0, 5%);
  }
}
@media (max-width: 1024px) {
  .hero {
    margin-bottom: 120px;
  }
}
@media (max-width: 768px) {
  .hero {
    padding-top: 60px;
  }
}
@media (max-width: 576px) {
  .hero {
    margin-bottom: 80px;
  }
}

.qualification {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}
.qualification__item {
  position: relative;
  font: var(--heavy-font);
  color: var(--color-port-gore);
}
.qualification__item:not(:last-child) {
  margin-right: var(--presecond-offset);
}
.qualification__item:not(:last-child)::after {
  content: "";
  position: absolute;
  right: -10px;
  top: 50%;
  display: block;
  width: 1px;
  height: 100%;
  background-color: var(--color-port-gore);
  transform: translateY(-50%);
}
@media (max-width: 576px) {
  .qualification__item {
    font-size: 16px;
    line-height: 1.8;
  }
}

.profession {
  position: relative;
}
.profession__wrapper {
  position: relative;
  z-index: var(--z-section);
}
.profession__desc {
  margin-bottom: var(--second-offset);
  font: var(--heavy-font);
  font-size: 27px;
  color: var(--color-port-gore);
}
@media (max-width: 768px) {
