.modal {
  position: fixed;
  left: 0;
  top: 0;
  z-index: var(--z-modal);
  overflow: auto;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  opacity: 0;
  visibility: hidden;
  transition:
    opacity 0.3s ease-in-out,
    visibility 0.3s ease-in-out;
  cursor: pointer;

  &__container {
    position: relative;
    display: flex;
    align-items: flex-start;
    justify-content: center;
    width: 100%;
    height: 100%;
  }

  &__img {
    margin: auto 0;
    width: 350px;
    height: 500px;
    cursor: default;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    @include mobile {
      margin-right: 0;
    }
  }

  &__close {
    position: absolute;
    right: 0;
    top: 20px;
    width: 30px;
    height: 30px;

    &.focus-visible {
      outline: 1px solid var(--color-white);
    }
  }

  &__line {
    position: absolute;
    display: block;
    width: 30px;
    height: 2px;
    background-color: var(--color-white);
    
    &:nth-child(1) {
      transform: rotate(45deg);
    }

    &:nth-child(2) {
      transform: rotate(-45deg);
    }
  }

  &--visible {
    opacity: 1;
    visibility: visible;
  }
}