.logo {
  display: flex;
  align-items: center;

  svg {
    width: 30px;
    height: 30px;
    object-fit: cover;
  }

  &__title {
    margin-left: var(--small-offset);
    font-weight: 800;
    font-size: 14px;
    line-height: 0.9;
    letter-spacing: 4px;

    span {
      font-size: 23px;
      letter-spacing: 3px;

      @include tablet {
        font-size: 20px;
      }

      @include mobile {
        font-size: 18px;
      }
    }

    @include mobile {
      margin-left: 10px;
      font-size: 12px;
    }
  }

  &__slogan {
    position: relative;
    margin-left: var(--presecond-offset);
    font-weight: 600;
    font-size: 18px;
    line-height: 1;
    letter-spacing: 1px;

    &::before {
      content: "";
      position: absolute;
      left: -15px;
      top: 50%;
      display: block;
      width: 1px;
      height: 80%;
      background-color: var(--color-radical-red);
      transform: translateY(-50%);

      @include tablet {
        left: -12px;
      }
    }

    @include tablet {
      font-size: 16px;
    }

    @include small-tablet {
      display: none;
    }
  }

  &__title,
  &__slogan {
    max-width: 150px;
    font-family: var(--font-family);
    text-transform: uppercase;
    color: var(--color-klein-blue);

    @include tablet {
      max-width: 135px;
    }
  }
}