.footer {
  padding: var(--supsecond-offset) 0;
  background-color: var(--color-bg);

  &__container {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
  }

  &__btn {
    display: flex;
    align-items: center;
    margin-right: var(--presecond-offset);
    padding: 14px 23px;

    svg {
      margin-right: var(--presecond-offset);
      width: 26px;
      height: 26px;
      object-fit: cover;
    }

    @include tablet {
      padding: 10px 15px;
    }

    @media (max-width: 367px) {
      margin-bottom: 20px;
    }
  }

  .logo {
    @media (max-width: 581px) {
      margin-bottom: 20px;
    }
  }

  .logo__title {
    color: var(--color-mine-shaft);
  }

  &__right {
    flex-wrap: wrap;
  }

  &__left,
  &__right {
    display: flex;
    align-items: center;
  }

  &__logo,
  &__btn {
    text-decoration: none;
  }

  @media (max-width: 367px) {
    --supsecond-offset: 25px;
  }
}