.header {
  padding-top: var(--second-offset);
  background-color: var(--color-bg);

  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__btn {
    display: flex;
    align-items: center;
    margin-right: var(--presecond-offset);
    padding: 14px 23px;

    svg {
      margin-right: var(--presecond-offset);
      width: 26px;
      height: 26px;
      object-fit: cover;
    }

    @include tablet {
      padding: 10px 15px;
    }
  }

  &__left,
  &__right {
    display: flex;
    align-items: center;
  }

  &__logo,
  &__btn {
    text-decoration: none;
  }

  @include small-tablet {
    &__right {
      display: none;
    }
  }
}